.page-container{
    justify-content: center;
    background-color:linear-gradient(#88dcfd, #5c79f8);
    display: flex;
    align-items: center;
    height: 100vh;
    position: relative;
}

.page-container h1{
    color: #3c009d;
    position: absolute;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
    white-space: nowrap;
    font-size: 24px;
    animation: slide-left 10s linear infinite;
}

@keyframes slide-left {
    0% {
        transform: translateX(100%);
    }
    100% {
        transform: translateX(-100%);
    }
}

.container{
    background-color: #eddcfc;
    padding: 120px 20px;
    width: 220px;
    height:400px;
    border-radius: 10px;
    align-content: center;
    box-shadow: 1px 1px 8px rgba(0, 0, 0, 2);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.signup-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #eddcfc;
    padding: 120px 20px;
    border-radius: 8px;
    width: 400px;
    height: auto;
    align-content: center;
    box-shadow: 1px 1px 8px rgba(0, 0, 0, 2);
}

.text{
    color: #27193f;
    font-size: 48px;
    font-weight: 700;
}

.signup-container h2{
    text-align: center;
    color: #614194;
    font-size: 24px;
    font-weight: 700;
    text-transform: uppercase;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
}

.input-group{
    width: 100%;
    display: flex;
    margin-bottom: 15px;
    flex-direction: column;
}

.input-group label{
    font-size: 14px;
    margin-top: 10px;
    color: #333;
}

.input-group input {
    padding: 10px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.submit-button {
    background-color: purple;
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    width: 100%;
}

.submit-button:hover {
    background-color: #5e2a8a;
}

/* Login link styling */
.login-link {
    margin-top: 1px;
    text-align: center;
    font-size: 14px;
    color: #333;
    cursor: pointer;
}

.login-link:hover {
    color: #5e2a8a;
}

.logo {
    margin-top: 20px;
    width: 150px;
    height: 100px;
    margin: 0 auto;
    display: block;
}

.header{
    position: relative;
    overflow: hidden;
    height: 50px;
    margin-bottom: 20px;
}

.header h1{
    position: absolute;
    white-space: nowrap;
    font-size: 24px;
    color: #3c009d;
    animation: slide-left 10s linear infinite;
}

